import React, {useMemo} from 'react';
import styled from 'styled-components';

import {Button, } from '@material-ui/core';
// import Button from '../../../components/Button';
// import Card from '../../../components/Card';
// import CardContent from '../../../components/CardContent';
import Value from '../../../components/Value';
import useEarnings from '../../../hooks/useEarnings';
import useHarvest from '../../../hooks/useHarvest';

import {getDisplayBalance} from '../../../utils/formatBalance';
import TokenSymbol from '../../../components/TokenSymbol';
import {Bank} from '../../../polydoge-finance';
import usePDDollarStats from '../../../hooks/usePDDollarStats';
import useShareStats from '../../../hooks/usePDShareStats';

interface HarvestProps {
  bank: Bank;
}

const Harvest: React.FC<HarvestProps> = ({bank}) => {
  const earnings = useEarnings(bank.contract, bank.earnTokenName, bank.poolId);
  const {onReward} = useHarvest(bank);
  const pddollarStats = usePDDollarStats();
  const tShareStats = useShareStats();

  const tokenName = bank.earnTokenName === 'PDSHARE' ? 'PDSHARE' : 'PDDOLLAR';
  const tokenStats = bank.earnTokenName === 'PDSHARE' ? tShareStats : pddollarStats;
  const tokenPriceInDollars = useMemo(
    () => (tokenStats ? Number(tokenStats.priceInDollars).toFixed(2) : null),
    [tokenStats],
  );
  const earnedInDollars = (Number(tokenPriceInDollars) * Number(getDisplayBalance(earnings))).toFixed(2);
  return (
    <div className='claimReward'>
      <div className='label pinkLabel'>Reward</div>
      <TokenSymbol symbol={bank.earnToken.symbol} />
      <div className='MY'>{`${tokenName} Earned`}</div>
      <Value value={getDisplayBalance(earnings, bank.earnToken.decimal, 2)} />
      <div className='MB'>{`â‰ˆ $${earnedInDollars}`}</div>
      <Button onClick={onReward} disabled={earnings.eq(0)} className={earnings.eq(0) ? 'shinyButtonDisabled' : 'shinyButtonSecondaryFarm'}>
        Claim
      </Button>
    </div>
  );
};

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`;
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`;

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`;

export default Harvest;
