import {useCallback, useEffect, useState} from 'react';

import {BigNumber} from 'ethers';
import usePolyDogeFinance from './usePolyDogeFinance';
import {ContractName} from '../polydoge-finance';
import config from '../config';

const useStakedBalance = (poolName: ContractName, poolId: Number) => {
  const [balance, setBalance] = useState(BigNumber.from(0));
  const polydogeFinance = usePolyDogeFinance();
  const isUnlocked = polydogeFinance?.isUnlocked;

  const fetchBalance = useCallback(async () => {
    const balance = await polydogeFinance.stakedBalanceOnBank(poolName, poolId, polydogeFinance.myAccount);
    setBalance(balance);
  }, [poolName, poolId, polydogeFinance]);

  useEffect(() => {
    if (isUnlocked) {
      fetchBalance().catch((err) => console.error(err.stack));

      const refreshBalance = setInterval(fetchBalance, config.refreshInterval);
      return () => clearInterval(refreshBalance);
    }
  }, [isUnlocked, poolName, setBalance, polydogeFinance, fetchBalance]);

  return balance;
};

export default useStakedBalance;
