import React from 'react';
import {Grid, Accordion, AccordionSummary, AccordionDetails, Button} from '@material-ui/core';
import useStatsForPool from '../../hooks/useStatsForPool';
import TokenSymbol from '../../components/TokenSymbol';
import Harvest from '../Bank/components/Harvest';
import Stake from '../Bank/components/Stake';
import useRedeem from '../../hooks/useRedeem';
import usePolyDogeFinance from '../../hooks/usePolyDogeFinance';
import './Farm.css';
const FarmCard = ({bank}) => {
  const { onRedeem } = useRedeem(bank);
  const statsOnPool = useStatsForPool(bank);
  const polydogeFinance = usePolyDogeFinance();
  const pddollarAddr = polydogeFinance.PDDOLLAR.address;
  const bshareAddr = polydogeFinance.PDSHARE.address;
  let pairName = '';
  let uniswapUrl = '';
  if (bank.depositTokenName.startsWith('PDDOLLAR-WMATIC')) {
    pairName = 'PDDOLLAR-WMATIC pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x0d500B1d8E8eF31E21C99d1Db9A6444d3ADf1270/' + pddollarAddr;
  } else if (bank.depositTokenName.startsWith('PDSHARE-ETH')) {
    pairName = 'PDSHARE-ETH pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x7ceB23fD6bC0adD59E62ac25578270cFf1b9f619/' + bshareAddr;
  } else if (bank.depositTokenName.startsWith('PDSHARE-WMATIC')) {
    pairName = 'PDSHARE-WMATIC pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x0d500B1d8E8eF31E21C99d1Db9A6444d3ADf1270/' + bshareAddr;
  } else if (bank.depositTokenName.startsWith('PDSHARE-PDDOLLAR')) {
    pairName = 'PDSHARE-PDDOLLAR pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x146e58D34EaB0bFf7e0a63cfe9332908d680c667/' + bshareAddr;
  } else if (bank.depositTokenName.startsWith('PDDOLLAR-USDC')) {
    pairName = 'PDDOLLAR-USDC pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x2791Bca1f2de4661ED88A30C99A7a9449Aa84174/' + pddollarAddr;
  } else if (bank.depositTokenName.startsWith('PDDOLLAR-POLYDOGE')) {
    pairName = 'PDDOLLAR-POLYDOGE pair';
    uniswapUrl = 'https://quickswap.exchange/#/add/0x8A953CfE442c5E8855cc6c61b1293FA648BAE472/' + pddollarAddr;
  }
  return (
    <Grid item xs={12}>
      <Accordion className='baseCard singlFarmCard'>
        <AccordionSummary>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={5}>
              <div className='titleLogoSingleFarm'>
                <div className='titleInfoSingleFarm'>
                  {bank.depositTokenName.toUpperCase()} Earn {` ${bank.earnTokenName}`}
                </div>
                <div className='logosBox'>
                  <TokenSymbol symbol={bank.depositTokenName} />
                </div>
              </div>
            </Grid>
            {/* <Grid item xs={4} sm={3}>
                <div className='depositedSingleFarm'>
                </div>
                 <div>
                  {bank.depositTokenName}
                </div> 
            </Grid> */}
            <Grid item xs={4} sm={3}>
              <div className='containerInfoSingleFarm'>
                <div className='titleInfoSingleFarm'>Daily APR</div>
                <div className='singleFarmApr'>{bank.closedForStaking ? '0.00' : statsOnPool?.dailyAPR}%</div>
              </div>
            </Grid>
            <Grid item xs={5} sm={3}>
              <div className='containerInfoSingleFarm'>
                <div className='titleInfoSingleFarm'>TVL</div>
                <div className='singleFarmTvl'>${statsOnPool?.TVL}</div>
              </div>
            </Grid>
            <Grid item xs={3} sm={1}>
              <div className='iconAccordion'>
              </div>
            </Grid>
        </Grid>
        </AccordionSummary>
        <AccordionDetails>
          <Grid container spacing={3} className='add2Farm'>
            <Grid item xs={12} sm={6} className='rewardSingleFarm'>
              <Harvest bank={bank} />
            </Grid>
            <Grid item xs={12} sm={6} className='stakeSingleFarm'>
              <Stake bank={bank} />
            </Grid>
            <Grid item xs={12}>
              <div className='claimAddLiquidity'>
                <div className={[bank.depositTokenName] + 'class addLiquidityPair'}>
                  <div className='MB'>Add liquidity to:</div>
                  <Button className='buttonAddLiquidity shinyButtonSecondary bgYellow ' href={uniswapUrl} target='_blank'>{pairName}</Button>
                </div>
                <div className='endStaking'>
                  <div className='MB'>End staking:</div>
                  <Button onClick={onRedeem} className="shinyButtonSecondary">
                    Claim &amp; Withdraw
                  </Button>
                </div>
              </div>
            </Grid>
          </Grid>
        </AccordionDetails>
      </Accordion>
    </Grid>
  );
};

export default FarmCard;