import { useCallback } from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import { Bank } from '../polydoge-finance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';
import { parseUnits } from 'ethers/lib/utils';
import { BigNumber } from 'ethers';

const useStake = (bank: Bank) => {
  const polydogeFinance = usePolyDogeFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleStake = useCallback(
    (amount: string) => {
      const amountBn = bank.sectionInUI !== 3 ? parseUnits(amount, bank.depositToken.decimal) : BigNumber.from(amount);
      if (bank.sectionInUI === 3) {
        handleTransactionReceipt(
          polydogeFinance.stake(bank.contract, bank.poolId,bank.sectionInUI, amountBn),
          `Buy ${amount} ${bank.depositTokenName} Node`,
        );
      } else {
        handleTransactionReceipt(
          polydogeFinance.stake(bank.contract, bank.poolId,bank.sectionInUI, amountBn),
          `Stake ${amount} ${bank.depositTokenName} to ${bank.contract}`,
        );
      }
    },
    [bank, polydogeFinance, handleTransactionReceipt],
  );
  return { onStake: handleStake };
};

export default useStake;
