import {useEffect, useState} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {LPStat} from '../polydoge-finance/types';
import useRefresh from './useRefresh';

const useLpStats = (lpTicker: string) => {
  const [stat, setStat] = useState<LPStat>();
  const {slowRefresh} = useRefresh();
  const polydogeFinance = usePolyDogeFinance();

  useEffect(() => {
    async function fetchLpPrice() {
      try {
        setStat(await polydogeFinance.getLPStat(lpTicker));
      } catch (err) {
        console.error(err);
      }
    }
    fetchLpPrice();
  }, [setStat, polydogeFinance, slowRefresh, lpTicker]);

  return stat;
};

export default useLpStats;
