import {useCallback} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {Bank} from '../polydoge-finance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useRedeem = (bank: Bank) => {
  const polydogeFinance = usePolyDogeFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleRedeem = useCallback(() => {
    handleTransactionReceipt(polydogeFinance.exit(bank.contract, bank.poolId), `Redeem ${bank.contract}`);
  }, [bank, polydogeFinance, handleTransactionReceipt]);

  return {onRedeem: handleRedeem};
};

export default useRedeem;
