import {useCallback, useState, useEffect} from 'react';
import usePolyDogeFinance from './usePolyDogeFinance';
import {Bank} from '../polydoge-finance';
import {PoolStats} from '../polydoge-finance/types';
import config from '../config';

const useStatsForPool = (bank: Bank) => {
  const polydogeFinance = usePolyDogeFinance();

  const [poolAPRs, setPoolAPRs] = useState<PoolStats>();

  const fetchAPRsForPool = useCallback(async () => {
    setPoolAPRs(await polydogeFinance.getPoolAPRs(bank));
  }, [polydogeFinance, bank]);

  useEffect(() => {
    fetchAPRsForPool().catch((err) => console.error(`Failed to fetch APR info: ${err.stack}`));
    const refreshInterval = setInterval(fetchAPRsForPool, config.refreshInterval);
    return () => clearInterval(refreshInterval);
  }, [setPoolAPRs, polydogeFinance, fetchAPRsForPool]);

  return poolAPRs;
};

export default useStatsForPool;
